/**
 * @class Reply
 */
class Reply {

  next: Function;
  ctx: Hemera;

  /**
   * Creates an instance of Reply.
   *
   * @param {Hemera} ctx
   *
   * @memberOf Reply
   */
  constructor(ctx: Hemera) {

    this.ctx = ctx
  }

  /**
   *
   *
   * @param {*} value
   *
   * @memberOf Reply
   */
  reply(value: any) {

    if (value instanceof Error) {

      this.next(value)
    } else {

      this.next(null, value)
    }

  }

  /**
   *
   *
   * @returns
   *
   * @memberOf Reply
   */
  continue () {

    this.next()

    return this
  }

}

module.exports = Reply
